<template lang="pug">
.gallery(gallery)
    .gallery__wrap
        ul.gallery__list(gallery-list)
            li(v-for="item, key in galleryItems" :key="key" )
                picture.picture.about__pic
                    source(type='image/webp' media='(min-width: 1140px)' :srcset="`${item.path}-lg.webp 1x, ${item.path}-lg@2x.webp 2x`")
                    source(type='image/webp' media='(min-width: 768px)' :srcset="`${item.path}-md.webp 1x, ${item.path}-md@2x.webp 2x`")
                    source(type='image/webp' :srcset="`${item.path}-sm.webp 1x, ${item.path}-sm@2x.webp 2x`")
                    source(media='(min-width: 1140px)' :srcset="`${item.path}-lg.jpg 1x, ${item.path}-lg@2x.jpg 2x`")
                    source(media='(min-width: 768px)' :srcset="`${item.path}-md.jpg 1x, ${item.path}-md@2x.jpg 2x`")
                    img(:src="`${item.path}-sm.jpg`" :srcset="`${item.path}-sm@2x.jpg 2x`" width='320' height='291' alt='')

        ul.gallery__pagination(v-for="item, key in galleryItems" :key="key" )
            li.gallery__pagination(data-gallery-page=`${key}`)
        .gallery__toggle.gallery__tooggle-prev(data-galley-toggle='-1')
        .gallery__toggle.gallery__tooggle-next(data-galley-toggle='1')
</template>

<script>
import { galleryItems } from '@/_config'
export default {
    data() {
        return {
            galleryItems: galleryItems
        }
    },
}
</script>